"use client"

import { useState, useEffect } from "react"
import Link from "next/link"
import { usePathname } from "next/navigation"

export function Navigation() {
  const [scrolled, setScrolled] = useState(false)
  const pathname = usePathname()

  useEffect(() => {
    const handleScroll = () => {
      setScrolled(window.scrollY > 50)
    }
    window.addEventListener("scroll", handleScroll)
    return () => window.removeEventListener("scroll", handleScroll)
  }, [])

  const isActive = (path: string) => pathname === path

  return (
    <nav className="fixed top-0 left-0 right-0 z-50 transition-all duration-300">
      <div className="max-w-6xl mx-auto px-6 lg:px-8 py-4">
        <div
          className={`relative rounded-full border transition-all duration-300 ${
            scrolled
              ? "border-white/60 bg-white/12 backdrop-blur-xl shadow-[0_18px_90px_-60px_rgba(0,0,0,0.65)]"
              : "border-white/30 bg-white/6 backdrop-blur-lg shadow-[0_10px_40px_-40px_rgba(0,0,0,0.55)]"
          }`}
        >
          {/* bubble edge refraction */}
          <div className="pointer-events-none absolute inset-0 rounded-full mix-blend-screen opacity-75 [mask-image:radial-gradient(160%_180%_at_0%_50%,black,transparent),radial-gradient(160%_180%_at_100%_50%,black,transparent)] bg-[radial-gradient(circle_at_0%_50%,rgba(255,255,255,0.9),rgba(255,255,255,0)_55%),radial-gradient(circle_at_100%_50%,rgba(255,255,255,0.9),rgba(255,255,255,0)_55%)] blur-lg" />
          <div className="pointer-events-none absolute inset-0 rounded-full opacity-25 bg-[radial-gradient(180%_220%_at_0%_60%,rgba(0,200,255,0.18),transparent),radial-gradient(180%_220%_at_100%_40%,rgba(255,120,180,0.18),transparent)] blur-2xl" />
          <div className="pointer-events-none absolute inset-0 rounded-full bg-linear-to-b from-white/12 via-white/6 to-transparent" />
          <div className="pointer-events-none absolute inset-0 rounded-full opacity-25 [mask-image:radial-gradient(90%_90%_at_50%_50%,black,transparent)] bg-[radial-gradient(circle_at_50%_50%,rgba(255,255,255,0.25),rgba(255,255,255,0)_60%)] blur-sm" />

          <div className="relative flex items-center justify-between px-6 py-4">
            <Link href="/" className="text-sm font-medium hover:opacity-70 transition-opacity">
              Portfolio
            </Link>

            <div className="flex items-center gap-8">
              <Link
                href="/work"
                className={`text-sm transition-colors ${
                  isActive("/work") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Work
              </Link>
              <Link
                href="/photos"
                className={`text-sm transition-colors ${
                  isActive("/photos") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Photos
              </Link>
              <Link
                href="/travel"
                className={`text-sm transition-colors ${
                  isActive("/travel") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Travel
              </Link>
              <Link
                href="/contact"
                className={`text-sm transition-colors ${
                  isActive("/contact") ? "text-foreground" : "text-muted-foreground hover:text-foreground"
                }`}
              >
                Contact
              </Link>
            </div>
          </div>
        </div>
      </div>
    </nav>
  )
}
